.content {
  width: 50%;
  max-width: 500px;
  margin-left: 27%;
  z-index: 22;
  /* overflow: hidden; */
}
.heading > h2 {
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.7;
  font-weight: 600;
}
.heading > h1 {
  font-size: 30px;
  margin-bottom: 1.5em;
}
.para {
  font-weight: 500;
  font-size: 14px;
}

@media (max-width: 1025px) {
}
@media (max-width: 640px) {
  .content {
    /* transform: translateY(100%); */
    width: 100vw !important;
    /* max-width: 500px; 
        */
    height: 70vh;
    position: relative;
    max-width: 100%;
    top: 60vh;
    color: #333;
    background-color: #f6f5e8;
    margin-left: 0%;
    margin-right: 0%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* margin: 0 auto; */
  }
}
