.header {
  width: 100%;
  height: 110vh;
  position: relative;
  display: flex;
  align-items: center;
}

.image {
  position: absolute;
  left: 0;
  /* overflow: hidden; */
  z-index: -1;
  top: 0;
  display: flex;
  height: 110vh;
  width: 100%;
  background-position: 50% 45%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("https://www.aesop.com/u1nb1km7t5q7/4XKiBRqSAuL4DBmtdfXqW2/7c0a219b75dc8d014e19ee426078f506/Aesop_Raeburn_Web_Homepage_Primary_Full_Bleed_GL_Desktop_2880x1044px.jpg");
}

@media (max-width: 1025px) {
  .image {
    /* background-color: #fffef2; */
    background-image: url("https://www.aesop.com/u1nb1km7t5q7/4m7uhaRUoEGvt4ABOKrSKi/287c2b26b483de654f27dd9ba96ab223/Aesop_Raeburn_Web_Homepage_Primary_Full_Bleed_GL_Tablet_1536x1730px.jpg");
  }
  .header {
    height: 100vh;
  }
}
@media (max-width: 640px) {
  .image {
    /* background-color: #fffef2; */
    /* height: 100vh; */
    height: 60vh;
    background-position: top;
    background-size: cover;
    /* background-position: center; */
    background-image: url("https://www.aesop.com/u1nb1km7t5q7/6uI0e52sXlthG9xb4lOuzR/87491cf06e2e2a021d49b267683f5681/Aesop_Raeburn_Web_Homepage_Primary_Full_Bleed_GL_Mobile_640x640px.jpg");
  }
  .header {
    display: block;
    height: 130vh;
  }
}
