.image {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  color: white !important;
  background-position: center;
  background-size: cover;
  line-height: 2;

  /* justify-content: center; */
}
.image > a {
  text-decoration: underline !important;
  cursor: pointer;
  margin-top: 40px;
}
.image > h2 {
  font-family: "Playfair Display", serif !important;
}
