.copywrite {
  border-top: 3px solid #fff;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  color: #fffef2;
  height: 60px;
  font-weight: 600;
  background-color: #252525;
}
.email {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 10px;
  border-radius: 3px;
  margin-bottom: 15px;
  outline: none !important;
  border: 1px solid #fffef2;
  background: none;
  color: #fffef2 !important;
}

::placeholder {
  color: #fffef2;
  opacity: 1 !important;
}

.notification {
  margin-right: 10px;
}

.notificationLabel {
  width: 100%;
  color: #fffef2;
  font-size: 12px;
}

.heading {
  font-size: 18px;
  padding-bottom: 10px;
  color: #fffef2;
  border-bottom: 1px solid #fffef2;
  margin-bottom: 10px;
  font-weight: 600;
}

.ul {
  min-height: 15vh;
  padding: 0% !important;
}
.li {
  list-style-type: none;
  text-transform: capitalize;
  color: #fffef2;
  margin: 10px 0;
  font-size: 12px;
  font-weight: 300;
}

@media (max-width: 1025px) {
}
@media (max-width: 640px) {
  .list {
    margin-top: 20px;
  }
}
