@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap");
.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 30px;
  padding-left: 30px;
  /* width: 90%; */
}
.content > div > h2 {
  font-size: 40px;
  font-family: "Playfair Display", serif !important;
  margin-bottom: 40px;
  padding-right: 30px !important;
}

.content > div > p {
  font-size: 16px;
}

.second {
  height: 90vh;
}

.image {
  width: 95%;
  /* height: 100vh; */
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  /* color: white !important; */
  background-position: center;
  background-size: cover;
  /* line-height: 2; */
  overflow-x: hidden;
  overflow-y: hidden;
  /* justify-content: center; */
}

@media (max-width: 1025px) {
  .image {
    right: 50%;
    transform: translate(50%, -50%);
  }
  .second {
    height: 60vh;
  }
}
@media (max-width: 640px) {
  .content > h2 {
    font-size: 25px;
  }
}
